import React from "react"
import { Link } from "gatsby"

const PostLink = ({ post }) =>   (
  <article>
    <>
    <span style={{display: "flex", flexDirection: "row", alignItems: "center"}}>
        <Link to={post.frontmatter.path}>
      {!!post.frontmatter.thumbnail && (
        <img style={{width: "30vw", padding: 10}} src={post.frontmatter.thumbnail} alt={post.frontmatter.title + "- Featured Shot"} />
      )}
        </Link>
        <div>
          <h2 className="post-title" style={{fontSize: "3vw"}}> 
          <Link to={post.frontmatter.path} className="post-link">
            {post.frontmatter.title}
          </Link>
          </h2>
          <p style={{lineHeight: "1", fontSize: "2vw"}}>{post.excerpt}</p>
          <div class="post-meta" style={{fontSize: "1.5vw", textTransform: "lowercase"}}>{post.frontmatter.date}</div>
        </div>
    </span>
    </>
  </article>
)
export default PostLink